import React, { useContext } from 'react'
import Navigation from './NavigationComponent'
import { MusicPlayListContext } from '../context/MusicPlayListContext'

const NowPlaying = () => {
  const { nowPlaying } = useContext(MusicPlayListContext)
  return (
    <div className="music-container play">
      <div className="music-info">
        <h4 id="title">{nowPlaying.track}</h4>
        <div className="progress-container">
          <div className="progress"></div>
        </div>
      </div>
      <audio
        src="https://s3.us-east-2.amazonaws.com/react-challenge/tracks/frank_ocean_sweet_life.mp3"
        id="audio"
      ></audio>
      <div className="img-container">
        <img
          src="data:image/jpeg;base64,"
          alt="music-cover"
          id="cove"
        />
      </div>
      <Navigation />
    </div>
  )
}

export default NowPlaying
